header {
  background: #fff;
  border: 1.5px solid #111;
  border-radius: 0 0 8px 8px;
  height: 64px;
  margin: auto;
  padding: 18px 20px;
  display: block;
}

.header-flex a {
  color: #111;
  text-decoration: none;
}

header ul {
  margin: 0;
  list-style: none;
  display: none;
}

#header-sign-button {
  display: none;
}

.header-nav-wrapper {
  margin-left: 0;
  display: flex;
}

.header-logo {
  align-items: center;
  gap: 8px;
  display: flex;
}

.header-logo-icon {
  width: 24px;
  height: 24px;
}

.header-logo-text {
  width: 77px;
  height: 13px;
}

.header-switch {
  width: 40px;
  height: 20px;
  display: inline-block;
  position: relative;
}

.header-switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.header-slider {
  cursor: pointer;
  background: linear-gradient(#4f2ee8 0%, #686868 100%);
  border-radius: 45px;
  padding: .5px;
  transition: all .4s;
  position: absolute;
  inset: 1.5px 0 0;
  box-shadow: inset 1px 1px 2px #0000001a;
}

.header-slider:before {
  content: "";
  background-color: var(--background-light);
  border-radius: 50%;
  width: 17px;
  height: 17px;
  padding: .5px;
  transition: all .4s;
  position: absolute;
  transform: translateX(21px);
}

input:checked + .header-slider {
  background: linear-gradient(#4f2ee8 0%, #dcdcdc 100%);
  box-shadow: inset 1px 1px 2px #0000001a;
}

input:checked + .header-slider:before {
  transform: translateX(1px);
}

.header-hamburger {
  stroke: var(--black-text-color);
  cursor: pointer;
  background-color: #0000;
  border: none;
  justify-content: end;
  width: 28px;
  height: 28px;
  padding: 0;
}

.header-log-in {
  border: 1.5px solid var(--black-text-color);
  background: var(--purple-accent-color);
  color: #fff;
  letter-spacing: -.56px;
  border-radius: 18px;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 90px;
  width: 184px;
  height: 48px;
  font-size: 14px;
  font-weight: 500;
  line-height: 18px;
  display: flex;
}

.header-flex {
