}

#mobileMenu {
  z-index: 1000;
  background: radial-gradient(189.32% 190.93% at 59.76% -23.42%, #4f2ee8 18.03%, #fff 100%);
  width: 100%;
  height: 100%;
  transition: transform .3s ease-in-out;
  display: none;
  position: absolute;
  top: 70px;
  left: 0;
  box-shadow: 0 2px 5px #0000001a;
}

.header-list-mobileMenu a {
  color: var(--background-light);
